import { requestApi } from '../request/request'
export function wxconfig (goodsData = {}, getAddr) {
  let url = window.location.href
  console.log('goodsData', goodsData)
  return new Promise((resolve, reject) => {
    requestApi({ name: 'getJSSDKConfig', data: { share_url: url } }).then(async (res) => {
      console.log(res.data, 'api/h5_order/share')
      wx.config({
        debug: false, // debug
        appId: res.data.appId, // appId
        timestamp: res.data.timestamp, // 
        nonceStr: res.data.nonceStr, // 
        signature: res.data.signature, // 
        jsApiList: [
          'onMenuShareAppMessage', // 
          'onMenuShareTimeline', // 
          'updateAppMessageShareData', // 
          'updateTimelineShareData', // 
          'openAddress' // 
        ]
      })
      wx.ready(async function () {
        if (getAddr) {
          let res = await openAddress()
          resolve(res)
        } else {
          shareConfig({
            title: goodsData.title, // 
            desc: goodsData.head_title, // 
            imgUrl: goodsData.head_img // 
          })
          resolve(true)
        }
      })
      wx.error(function (res) {
        console.error('', res)
        reject(res)
        // configerrorconfigdebugresSPA
      })
    })
  })
}

